import React, { useState } from "react"
import { Link } from "gatsby"
import Logo from "../assets/images/logo.svg"
import HamburgerMenu from "../assets/images/menu.svg"
import HamburgerClose from "../assets/images/close.svg"

const Header = () => {
    const [menuToggle, setMenuToggle] = useState(0)
    const toggleFunction = e => {
        e.preventDefault()
        setMenuToggle(!menuToggle)
    }
    return (
        <header className="header">
            <Link to="/" className="logo">
                <img src={Logo} alt="panbehkar logo" />
            </Link>
            <nav className="nav">
                <ul>
                    <li>
                        <Link activeClassName="active" to="/">
                            Home
                        </Link>
                    </li>
                    <li>
                        <Link
                            activeClassName="active"
                            to="/about"
                            partiallyActive={true}
                        >
                            About
                        </Link>
                    </li>
                    <li>
                        <Link
                            activeClassName="active"
                            to="/portfolio"
                            partiallyActive={true}
                        >
                            Portfolio
                        </Link>
                    </li>
                    <li>
                        <Link
                            activeClassName="active"
                            to="/contact"
                            partiallyActive={true}
                        >
                            Contact
                        </Link>
                    </li>
                    <li>
                        <Link
                            activeClassName="active"
                            to="/blog"
                            partiallyActive={true}
                        >
                            Blog
                        </Link>
                    </li>
                </ul>
            </nav>
            <nav className="nav-mobile">
                <a
                    href="#/"
                    className="hamburger-menu"
                    onClick={toggleFunction}
                >
                    <img src={HamburgerMenu} alt="menu"></img>
                </a>
                <div className={`overlay${menuToggle ? " open" : ""}`}>
                    <a
                        href="#/"
                        className="hamburger-close"
                        onClick={toggleFunction}
                    >
                        <img src={HamburgerClose} alt="close"></img>
                    </a>
                    <ul>
                        <li>
                            <Link activeClassName="active" to="/">
                                Home
                            </Link>
                        </li>
                        <li>
                            <Link
                                activeClassName="active"
                                to="/about"
                                partiallyActive={true}
                            >
                                About
                            </Link>
                        </li>
                        <li>
                            <Link
                                activeClassName="active"
                                to="/portfolio"
                                partiallyActive={true}
                            >
                                Portfolio
                            </Link>
                        </li>
                        <li>
                            <Link
                                activeClassName="active"
                                to="/contact"
                                partiallyActive={true}
                            >
                                Contact
                            </Link>
                        </li>
                        <li>
                            <Link
                                activeClassName="active"
                                to="/blog"
                                partiallyActive={true}
                            >
                                Blog
                            </Link>
                        </li>
                    </ul>
                </div>
            </nav>
        </header>
    )
}
export default Header
